.modal {
    $block-name: &; // #{$block-name}__element

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: $z-index-modal;

    &--active {
        visibility: visible;
        opacity: 1;
        transition: all 0.3s;
    }

    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    &__content {
        position: relative;
        max-width: 470px;
        padding: 60px 15px;
        background-color: $white;
        @include media-up--S {
            padding: 60px;
        }
    }

    &__close-btn {
        position: absolute;
        content: "";
        background: 0 0;
        right: 25px;
        top: 18px;
        border: none;
        z-index: 100;
        width: 20px;
        height: 20px;
        padding: 0;
        cursor: pointer;

        @include media-up--S {
            right: 14px;
            top: 14px;
        }

        &::before,
        &::after {
            background-color: #909197;
            content: "";
            position: absolute;
            top: 10px;
            right: 0;
            width: 20px;
            height: 2px;
            box-shadow: none;
            border-radius: 1px;
        }

        &::before {
            transform: rotate(45deg);
        }

        &::after {
            transform: rotate(-45deg);
        }

        &:hover {
            &::before,
            &::after {
                background-color: darken(#909197, 20%);
            }
        }
    }

    &__title {
        margin-bottom: 100px;
        font-size: 16px;
        line-height: 1.5;

        @include media-up--S {
            margin-bottom: 30px;
        }
    }
}

.modal__disclaimer {
    margin-top: 20px;
    font-size: 13px;
    line-height: 1.3;
}
