  right: auto;
  bottom: auto;
  width: 52%; }
  @media screen and (min-width: 768px) {
    .hero__image--2 {
      top: auto;
      left: auto;
      right: -7%;
      bottom: 6%;
      height: 40%;
      width: 60%; } }

